import {fetch} from './ajax'


const baseURL=localStorage.getItem('apiUrl');
const PREFIX=`/storesense/api/v1.0`;
function timeURL(url) {
  var time = (new Date()).valueOf();
  if(url.indexOf('?')>=0){
    url = url +'&t='+time;
  }else{
    url = url + '?t=' + time;
  }
  return url;
}
class Device{
  static getDeviceList(data){
    let url=`/device/find`;
    let config={
      url:timeURL(`${baseURL}${PREFIX}${url}`),
      data:data,
      method:'PUT'
    };
    return fetch(config);
  }

  static getUnassignDevice(){
    let url=`/device/unassign`;
    let config={
      url:timeURL(`${baseURL}${PREFIX}${url}`),
      method:'GET'
    }
    return fetch(config);
  }
   static addDevice(data){
    let url=`/device/add`;
    let config={
      url:timeURL(`${baseURL}${PREFIX}${url}`),
      data:data,
      method:'POST'
    };
    return fetch(config);
  }
  static editDevice(data){
    let url=`/device/edit`;
    let config={
      url:timeURL(`${baseURL}${PREFIX}${url}`),
      data:data,
      method:'PUT'
    };
    return fetch(config);
  }

  static deleteDevice(agentID){
    let url=`/device/delete?agentId=${agentID}`;
    let config={
      url:timeURL(`${baseURL}${PREFIX}${url}`),
      method:'DELETE'
    }
    return fetch(config);
  }
  static getDeviceDetail(agentID){
    let url=`device/detail?agentId=${agentID}`;
    let config={
      url:timeURL(`${baseURL}${PREFIX}${url}`),
      method:'GET'
    }
    return fetch(config);
  }
}
export default Device;



