import {fetch} from './ajax'
const baseURL=localStorage.getItem('apiUrl');
//const baseURL="http://172.21.81.160:8085";
const PREFIX="/storesense/api/v1.0";
function timeURL(url) {
    var time = (new Date()).valueOf();
    if(url.indexOf('?')>=0){
      url = url +'&t='+time;
    }else{
      url = url + '?t=' + time;
    }
    return url;
}
 class Cloud{

    static getConfig(){
        let url=`/setting`;
        let config={
            url:timeURL(`${baseURL}${PREFIX}${url}`),
            method:'GET'
        };
        return fetch(config);
    }

    static setConfig(data){
        let url=`/setting`;
        let config={
            url:timeURL(`${baseURL}${PREFIX}${url}`),
            data:data,
            method:'PUT'
        }
        return fetch(config);
    }

    static getStatus(){
        let url=`/setting/status`;
        let config={
            url:timeURL(`${baseURL}${PREFIX}${url}`),
            method:'GET'
        };
        return fetch(config);
    }   
}
export default Cloud;


