<template>
  <div class="header">
    <div class="el-logo">Store.SENSE</div>
    <el-breadcrumb separator="|">
      <el-breadcrumb-item v-for="item in breadList"
                          :to="{path:item.path}">{{item.name}}</el-breadcrumb-item>
    </el-breadcrumb>

    <!--<span class="el-user">admin</span>-->
    <!--<i class="iconfont icon-moreunfold" id="icon-login"></i>-->
    <img :src="imgUrl" class="icon-remind">
    <i class="iconfont icon-down-trangle" id="icon-trangle"></i>
    <span class="el-user">admin</span>
    <div class="el-line-right"></div>
    <i class="iconfont icon-lingdangcopy" id="icon-user"></i>
    <!--<i class="iconfont icon-remind" id="icon-remind"></i>-->

    <div class="el-line-left"></div>
  </div>
</template>

<script>
    import PubSub from 'pubsub-js';
    export default {
        name: "HeaderBar",
        data(){
          return {
            breadList: [],
            imgUrl:require("../assets/admin.png")
          }
        },
        mounted(){
          PubSub.subscribe("updateHeader",(msg,title) =>{
            if(title.path != ''){
              this.breadList = [];
            }

            this.breadList.push(title);
          });
        }
    }
</script>

<style scoped>
  .icon-logininfo{
    width: 150px;
    height: 60px;
    display: inline;
  }
  .header{
    text-align: left;
    margin-left: -20px;
    margin-right: -20px;
    height: 60px;
  }

  .el-logo{
    margin-left: 0px;
    background: #1C7FDC;
    text-align: center;
    color: white;
    width: 15%;
    font-size:26px;
    display: inline-block;
  }

  .el-breadcrumb{
    margin-left: 20px;
    display: inline-block;
  }

  .icon-remind{
    float: right;
    margin-right: 40px;
    width: 36px;
    margin-top: 10px;
  }

  #icon-login{
    float: right;
    margin-right: 20px;
    color: #1f23ff;
    font-size: 20px;
  }

  #icon-user{
    float: right;
    margin-right: 18px;
    color: #CDD7E3;
    font-size: 22px;
  }

  .el-line-left{
    float: right;
    margin-right: 18px;
    width: 2px;
    height: 60px;
    background: #E2E8F4;
  }

  .el-line-right{
    float: right;
    margin-right: 40px;
    width: 2px;
    height: 60px;
    background: #E2E8F4;
  }

  .el-user{
    float: right;
    margin-right: 10px;
    /*position: relative;*/
    /*left: 976px;*/
  }
  #icon-trangle{
    float: right;
    margin-right: 20px;
    /*position: relative;*/
    /*left: 980px;*/
  }
</style>



