<template>
  <div class="el-device-mgr">
    <div class="el-device-header">
      <div class="el-device-ctrl">
        <p class="el-group-info"  v-model="groupCount">{{groupCount}}</p>
        <p class="el-device-info"  v-model="deviceCount">{{deviceCount}}</p>
      </div>
      <div class="el-group-list">
        <el-tooltip :disabled="disabled" content="" placement="right" effect="dark">
         <i class="el-icon-plus" id="el-group-plus" @click="createGroup()"></i>
        </el-tooltip>
        <i class="el-icon-arrow-down"></i>
        <el-button class="el-group-all" type="text" @click="isGroupActive(-1)">
          <span :class="[groupIndex == -1 ? 'el-active' : 'el-default']"></span>
        </el-button>
        <div v-for="(item,index) in groupList" :key="index" >
          <el-button class="el-group-btn" type="text"  @click="isGroupActive(index)">
            <span :class="[groupIndex == index ? 'el-active' : 'el-default']">{{item.name}}</span>
            <span class="el-number" :class="[groupIndex == index ? 'el-number-active' : 'el-number-default']">
              {{item.count}}
            </span>
          </el-button>
        </div>
      </div>
      <div style="background-color: #EAEDF2"><hr class="el-split-line"></div>
      <div class="el-device-list">
        <el-tooltip :disabled="disabled" content="" placement="right" effect="dark">
          <i class="el-icon-plus" id="el-device-plus" @click="createDevice()"></i>
        </el-tooltip>
        <i class="el-icon-arrow-down"></i>
        <el-button class="el-device-all" type="text" @click="isDeviceActive(-1)">
          <span  :class="[deviceIndex == -1 ? 'el-active' : 'el-default']"
                 v-show="groupList.length > 0 && groupIndex!=-1">
            -
          </span>
        </el-button>
        <span class="el-device-empty" v-show="groupList.length == 0 || groupIndex == -1">...</span>
        <div v-for="(item,index) in deviceList" :key="index">
          <el-button class="el-device-btn" type="text" @click="isDeviceActive(index)">
            <span :class="[deviceIndex == index ? 'el-active' : 'el-default']">{{item.name}}</span>
            <span class="el-number" :class="[deviceIndex == index ? 'el-number-active' : 'el-number-default']">
              {{item.count}}
            </span>
          </el-button>
        </div>
      </div>
    </div>
    <div class="el-device-show">
      <el-dropdown size="medium" split-button type="primary">
        
        <el-dropdown-menu slot="dropdown" >
          <el-dropdown-item @click.native="addDevice">
          </el-dropdown-item>
          <el-dropdown-item @click.native="assignDevice">
          </el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <el-button class="el-dev-btn" type="primary" size="medium" @click="moveDevice"></el-button>
      <el-button class="el-dev-btn" id="delete-btn" type="primary" size="medium" @click="deleteDevices">&nbsp&nbsp&nbsp</el-button>
      <el-input v-model="input" placeholder="" @keyup.enter.native="searchDevice">
        <i @click="searchDevice" slot="prefix" class="el-input__icon el-icon-search"></i>
      </el-input>

      <el-table
        ref="multipleTable"
        :data="tableData"
        tooltip-effect="dark"
        height="481px"
        style="width: 100%;height: 481px;overflow-y: auto"
        @selection-change="handleSelectionChange">
        <el-table-column
          type="selection"
          header-align="center"
          width="80">
        </el-table-column>
        <el-table-column
          align="left"
          prop="status"
          label=""
          min-width="12%">
          <!--<template slot-scope="scope">{{ scope.row.status }}</template>-->
          <template slot-scope="scope">
            <i class="iconfont icon-shebeizhuangtai"
               v-if="scope.row.status=== 'Connected'"
               v-bind:class="'icon-status-con'">
            </i>
            <i class="iconfont icon-shebeizhuangtai"
               v-else v-bind:class="'icon-status-dis'">
            </i>
          </template>
        </el-table-column>
        <el-table-column
          align="left"
          prop="name"
          label=""
          min-width="22%">
        </el-table-column>
        <el-table-column
          align="left"
          prop="type"
          label="OS"
          min-width="28%"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          align="left"
          prop="model"
          label=""
          min-width="23%">
        </el-table-column>
        <el-table-column
          align="left"
          label=""
          min-width="15%">
          <template slot-scope="scope">
            <!--<i class="el-icon-edit-outline"  @click="handleEdit(scope.$index, scope.row)"></i>-->
             <i class="iconfont icon-bianji" style="font-size: 20px;"  @click="handleDetail(scope.$index, scope.row)"></i>
             <i style="margin-left: 15px ;font-size: 21px;" class="iconfont icon-shanchu"  @click="handleDelete(scope.$index, scope.row)"></i>
          </template>
        </el-table-column>
      </el-table>

      <el-pagination class="el-device-page" v-show="showPagin"
                     style="margin-bottom: 10px"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :page-size="currentSize"
        :current-page="currentPage"
        background
        layout="prev, pager, next"
        :total="pageCount">
      </el-pagination>
    </div>
    <div>
      <el-dialog title="" :visible.sync="moveDlgVisible" class="el-move-dialog"
                 width=28% :show-close="false" @open="moveDlgInit()">
        <div slot="title" class="el-move-text">
          <div class="move-dlg-title"></div>
          <i class="el-icon-close" id="icon-move-close" @click="moveDlgClose()"></i>
        </div>
        <div slot="" class="el-move-body" style="overflow-y: auto;overflow-x: hidden">
          <div v-for="(item,index) in movGroupList" :key="index" >
            <div class="move-group-btn" type="text" @click="movGroupSelect(index)">
              <i class="el-icon-caret-right" id="move-icon-right" v-show="!item.subShow"></i>
              <i class="el-icon-caret-bottom" id="move-icon-bottom" v-show="item.subShow"></i>
              <span class="move-btn-group">{{item.name}}</span>
            </div>
            <div class="move-device-all" v-for="(item,devIndex) in movDeviceList" :key="devIndex"
                 v-show="movGroupList[index].subShow">
              <div class="move-device-btn" type="text" @click="movDeviceSelect(devIndex)">
                <span class="move-device-name" :class="[movDeviceIndex == devIndex ? 'mov-device-active' : 'mov-device-default']">{{item.name}}</span>
              </div>
            </div>
          </div>
          <span class="el-move-lable" v-show="movGroupList.length !== 0 ? false : true">...</span>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button style="width: 100px" @click="moveDlgCancel"> </el-button>
          <el-button style="width: 100px" type="primary" v-bind:disabled="confirmDisabled" v-if="movDeviceIndex === -1 ? true :false" @click="moveDlgConfirm()"></el-button>
          <el-button style="width: 100px" type="primary" v-else @click="moveDlgConfirm()"></el-button>
        </span>
      </el-dialog>
    </div>
    <div>
      <el-dialog title="" :visible.sync="groupDlgVisible" class="el-group-dialog"
                 width=28% :show-close="false">
        <div slot="title" class="el-move-text">
          <div class="move-dlg-title"></div>
          <i class="el-icon-close" id="icon-group-close" @click="groupDlgClose"></i>
        </div>
        <div class="group-dlg-body">
          <div>
            <span class="group-lable-name"></span>
            <el-input class="group-input-name" v-model="inputGroupName" placeholder=""></el-input>
          </div>
          <div>
            <span class="group-lable-description"></span>
            <el-input class="group-input-description" v-model="inputGroupDescription" placeholder=""
              type="textarea" rows=7></el-input>
          </div>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button style="width: 100px" @click="groupDlgVisible = false"> </el-button>
          <el-button style="width: 100px" type="primary" @click="groupDlgConfirm()"></el-button>
        </span>
      </el-dialog>
    </div>
    <div>
      <el-dialog title="" :visible.sync="deleteDlgVisible" class="el-delete-dialog"
                 width=25% :show-close="false">
        <div slot="title" class="el-move-text">
          <div class="move-dlg-title"></div>
          <i class="el-icon-close" id="icon-delete-close" @click="deleteDlgClose()"></i>
        </div>
        <div class="delete-dlg-body">
          <div>
            <i class="el-icon-warning" id="delete-dlg-icon"></i>
            <span class="delete-lable-text"></span>
          </div>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button style="width: 100px" @click="deleteDlgVisible = false"> </el-button>
          <el-button style="width: 100px" type="primary" @click="deleteDevicesConfrim"></el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
  import PubSub from 'pubsub-js';
  import Vue from 'vue';
  import Device from '../api/device';
  import Group from '../api/group';
  export default {
    name: "DeviceMgr",

    data(){
      return{
        pageCount:0,
        groupCount: 0,
        deviceCount: 0,
        groupList:[],
        deviceList:[],
        groupIndex: -1,
        deviceIndex: -1,
        currentSize:25,
        currentPage:0,
        movGroupList:[],
        movGroupIndex: -1,
        movDeviceIndex: -1,
        movDeviceList:[],
        movDeviceID:0,
        tableData:[],
        multipleSelection: [],
        moveDlgVisible:false,
        groupDlgVisible:false,
        createIndex: -1,
        inputGroupName:'',
        inputGroupDescription:'',
        deleteDlgVisible:false,
        input:"",
        showPagin:true,
        confirmDisabled:true,
        baseURL:""
      }
    },
    inject:['reload'], //reload
    methods: {
      createGroup() {
        var self=this;
        self.createIndex = 0;
        self.inputGroupName="";
        self.inputGroupDescription="";
        if(self.groupList.length>=5){
          self.message("5!","warning",false);
          return false;
        }
        self.groupDlgVisible = true;
      },
      createDevice() {
        var self=this;
        self.inputGroupName="";
        self.inputGroupDescription="";
        if (self.groupList.length == 0) {
           self.message("...","warning",false);
           return false;
        }
        else if (self.groupIndex == -1) {
           self.message("...","warning",false);
           return false;
        }
        self.createIndex = 1;
        if(self.deviceList.length>=5){
          self.message("5!","warning",false);
          return false;
        }
        self.groupDlgVisible = true;
      },
      groupDlgClose() {
        this.groupDlgVisible = false;
      },
      /**
       * confirm create a new group
       */
       groupDlgConfirm() {
        var self = this;
        var flag=false;
        //var url=self.baseURL+ "/group/add";
        var nameMsg=self.regexName(self.inputGroupName);  //regex the input group name
        var desMsg=self.regexDescription(self.inputGroupDescription); //regex the input group description
        var obj={};
        let data = {};
        obj.id=-2;
        obj.name=self.inputGroupName;
        obj.description=self.inputGroupDescription;
        obj.count=0;
        if(self.inputGroupName.length==0){
           self.message("","warning",false);
           return false;
        }
        if(nameMsg.length!=0){
           self.message(nameMsg,"warning",false);
           return false;
        }
        if(desMsg.length!=0){
          self.message(desMsg,"warning",false);
          return false;
        }
        if (self.createIndex == 0) {
          if(self.groupList.length>=5){
            self.message("5!","warning",false);
            return false;
          }
          $.each(self.groupList,function(){
            if(this.name==self.inputGroupName){
              flag=true;
            }
          })
          if(flag){
            self.message("!","warning",false);
            return false;
          }
          data = {"name": this.inputGroupName, "description": this.inputGroupDescription, "parentId": -1};
          let indexTemp=self.groupList.length-1;
         // Vue.axios.post(url, data).then((res) => {
           Group.addGroup(data).then(res=>{
            if (res.data.result) {
              self.groupIndex=indexTemp+1;
              self.groupList.push(obj);
              self. init();  //load the parent level  group.
              self. isGroupActive(self.groupIndex);  
              self.deviceList.length=0;
              self.message("!","success",false);
              self.groupDlgVisible = false;
            }
          }).catch(err=>{
            self.message("","error",false);
            return false;
          })
        }
        else {
          if(self.deviceList.length>=5){
            self.message("5!","warning",false);
            return false;
          }
          $.each(self.deviceList,function(){
            if(this.name==self.inputGroupName){
              flag=true;
            }
          })
          if(flag){
            self.message("!","warning",false);
            return false;
          }
          var parentId = this.groupList[this.groupIndex].id;
          data = {"name": this.inputGroupName, "description": this.inputGroupDescription, "parentId": parentId};
          //var url = self.baseURL + "/group/add";
          //Vue.axios.post(url, data).then((res) => {
          Group.addGroup(data).then((res)=>{
            if (res.data.result) {
              self.deviceList.push(obj);
              self.message("!","success",false);
              self.groupDlgVisible = false;
            }
          }).catch(err=>{
            self.message("","error",false);
            return false;
          })
        }
        self.groupDlgVisible = false;
      },
      isGroupActive(index) {
        let self=this;
        console.log(index);
        self.groupIndex = index;
        self.deviceIndex=-1;
        if (index == -1) {
          self.deviceList.length = 0;
          self.getDataByGroup("");
        }
        else {
          var tempData = [];
          var groupId = self.groupList[index].id;
          //var url_getgroup = self.baseURL + "/group/detail?groupId=" + groupId;
          self.getDataByGroup(groupId);
          //Vue.axios.get(url_getgroup).then(res => {
          Group.getGroupDetail(groupId).then((res)=>{
            var list = res.data.children;
            $.each(list, function (i) {
              var obj = {};
              obj.id = list[i].id;
              obj.name = list[i].name;
              obj.description = list[i].description;
              obj.count = list[i].count;
              tempData[i] = obj;
            })
            self.deviceList = tempData;
          })
          .catch(err => {
            console.log(err.message);
          })
        }
      },

      isDeviceActive(index) {
        this.deviceIndex = index;
        var groupId = 0;
        if (index == -1) {
          groupId = this.groupList[this.groupIndex].id;
        } else {
          groupId = this.deviceList[index].id;
        }
        this.getDataByGroup(groupId);
      },
       getChildGroup(index) {
        let self=this;
        console.log(index);
        self.groupIndex = index;
        if (index == -1) {
          self.deviceList.length = 0;
          self.getDataByGroup("");
        }
        else {
          var tempData = [];
          var groupId = self.groupList[index].id;
          //var url_getgroup = self.baseURL + "/group/detail?groupId=" + groupId;
          self.getDataByGroup(groupId);
          //Vue.axios.get(url_getgroup).then(res => {
          Group.getGroupDetail(groupId).then((res)=>{
            var list = res.data.children;
            $.each(list, function (i) {
              var obj = {};
              obj.id = list[i].id;
              obj.name = list[i].name;
              obj.description = list[i].description;
              obj.count = list[i].count;
              tempData[i] = obj;
            })
            self.deviceList = tempData;
          })
          .catch(err => {
            console.log(err.message);
          })
        }
      },
      /**
       It is grouped by the group Id.
      */
      getDataByGroup(groupId) {
        var self = this;
        //var url_getData = self.baseURL + "/device/find";
        var tempData = [];
        let groupData = {
          groupId: groupId,
          page: self.currentPage == 0 ? 0 : self.currentPage - 1,
          size: self.currentSize,
          like: ""
        };
        // let config = {
        //   method: 'put',
        //   url: url_getData,
        //   headers: {
        //     'Content-Type': 'application/json; charset=UTF-8',
        //     'Accept': 'application/json',
        //   },
        //   data: groupData
        // }
        //Vue.axios(config).then((res) => {
        Device.getDeviceList(groupData).then((res)=>{  
          var list = res.data.content;
          if (list.length == 0) {
            self.showPagin = false;
          } else {
            self.showPagin = true;
          }
          self.pageCount = res.data.totalPages * self.currentSize;
          $.each(list, function (i) {
            var obj = {};
            obj.agentId = list[i].agentId;
            obj.status = list[i].status;
            obj.name = list[i].name;
            obj.type = list[i].osVersion;
            obj.model = list[i].model;
            obj.description = list[i].description;
            obj.cpuName = list[i].cpuName;
            obj.hddName = list[i].hddName;
            obj.boardName = list[i].boardName;
            tempData[i] = obj;
          })
          this.tableData = tempData;
          //this.deviceCount=res.data.totalElements;
        }).catch(error => {
          console.log(error);
        })
      },
      addDevice() {
        var header = {"path": "", "name": ""};
        PubSub.publish("updateHeader", header);
        let self=this;
        self.$router.push({name: 'AddDevice', params: {label: true, itemData: null}});
        //self.$router.push({name: 'AddDevice', params: {label: true, itemData: null,groupId:self.deviceIndex==-1?"":self.deviceList[self.deviceIndex].id}});
      },
      assignDevice() {
        var header = {"path": "", "name": ""};
        PubSub.publish("updateHeader", header);
        let self=this;
        //self.$router.push({name: 'AssignDevice',params:{groupId:self.deviceIndex==-1?0:self.deviceList[self.deviceIndex].id}});
        self.$router.push({name: 'AssignDevice'});
      },
      /**
       * initialize the group list.
       */
      init: function () {
        let self = this;
        let tempData = [];
        //let url = self.baseURL + "/group/list";
        // Vue.axios.get(url).then((res) => {
        Group.getGroupList().then((res)=>{
          let result = res.data;
          self.groupCount = result.totalCount;
          let groupList = result.data;
          let count = 0;
          $.each(groupList, function (i) {
            var obj = {};
            obj.id = groupList[i].id;
            obj.name = groupList[i].name;
            obj.description = groupList[i].description;
            obj.count = groupList[i].count;
            tempData[i] = obj;
            count = count + groupList[i].count;
          })
          self.groupList = tempData;
        }).catch(err => {
          console.log(err.message);
        })
        //}).catch((error)=>{console.log(error)});
      },
      /**
       * initialize device data Lists.
       */
      getDeviceCount() {
        var self = this;
        var url = self.baseURL + "/device/list";
        Vue.axios.get(url).then((res) => {
          self.deviceCount = res.data.totalCount;  //The device count
        })
      },
      handleSizeChange(va1) {
        this.currentSize = va1;
        this.searchDevice();
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.searchDevice();
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;  //The checked devices
      },
      /**
       * initialize group data
       */
      initGroupData() {
        var self = this;
        //let url = self.baseURL + "/group/list";
        var tempData = [];
       // Vue.axios.get(url).then((res) => {
        Group.getGroupList().then((res)=>{
          var groupList = res.data.data; //
          //
          $.each(groupList, function (i) {
            var obj = {};
            obj.id = groupList[i].id;
            obj.name = groupList[i].name;
            obj.description = groupList[i].description;
            obj.count = groupList[i].count;
            obj.subShow = false;
            tempData[i] = obj;
          })
          self.movGroupList = tempData;
        }).catch(err => {
          console.log(err.message);
        })
      },
      //move device
      moveDlgInit() {
        this.movGroupIndex = -1;
        this.movGroupList.forEach(function (item) {
          item.subShow = false;
        });
        this.movDeviceIndex = -1;
      },
      moveDevice() {
        var self=this;
        if (self.multipleSelection.length == 0) {
          self.message("!","warning",false);
          return false;
        } else {
          self.moveDlgVisible = true;
          self.initGroupData(); //get the move group list
        }
      },
      moveDlgCancel() {
        this.moveDlgVisible = false;
      },
      moveDlgClose() {
        this.moveDlgVisible = false;
      },
      //confirm move the device to the group.
       moveDlgConfirm() {
        var self = this;
        //var url = self.baseURL + "/group/put_device";
        self.moveDlgVisible = false;
        var arrID = [];
        $.each(self.multipleSelection, function (i) {
          arrID.push(this.agentId);
        })
        var groupId = self.movDeviceID; //The groupID
        let data = {"groupId": groupId, "agentId": arrID};
        //Vue.axios.post(url, data).then((res) => {
        Group.putDevice(data).then((res)=>{
          if (res.data.result) {
            self.message("!", "success", false);
          }
           self.init();    //
           self.getDeviceCount();
           self.getChildGroup(self.groupIndex);
           self.isDeviceActive(self.deviceIndex);
        })

      },
      /**
       * Two level group are obtained by the first level group ID
       * @param The level group index.
       */
      get2GroupById(index) {
        let self=this;
        var parentId = index;
          //var url = self.baseURL + "/group/detail?groupId=" + parentId;
          var tempData = [];
         // Vue.axios.get(url).then(res => {
          Group.getGroupDetail(parentId).then(res=>{
            var result = res.data;
            var list = res.data.children;
            $.each(list, function (i) {
              var obj = {};
              obj.id = list[i].id;
              obj.name = list[i].name;
              obj.description = list[i].description;
              obj.count = list[i].count;
              obj.editShow = false;
              tempData[i] = obj;
            })
            this.movDeviceList = tempData;
          }).catch(err => {
            console.log(err.message);
          })
      },
       movGroupSelect(index) {
        var self=this;
        self.movDeviceIndex = -1
        var isShow = this.movGroupList[index].subShow;
        this.movGroupList.forEach(function (item) {
          item.subShow = false;
          self.confirmDisabled=true;
        });
        this.movGroupList[index].subShow = !isShow;
        this.movGroupIndex = index;
        var groupParentId = this.movGroupList[index].id;
        this.movDeviceList = this.get2GroupById(groupParentId); //
      },
      movDeviceSelect(index) {
        this.confirmDisabled=false;
        this.movDeviceIndex = index;
        this.movDeviceID = this.movDeviceList[index].id;   //
      },

      //delete device
      /**
       * delete device by device id
       * @param deviceID
       */
      deleteDevice(deviceID) {
        var flag = false;
        let self=this;
        var url = self.baseURL + "/device/delete?agentId=";
        if (deviceID != undefined && deviceID.length != 0) {
          url += deviceID;
          $.ajax({
            url: url,
            type: 'DELETE',
            async: false,
            dataType: 'json', //
            success: function (res) {
              if (res.result) {
                flag = true;
              }
            }
          })
        }
        return flag;
      },
      //delete devices in batches
      deleteDevices() {
        var self = this;
        var selector = this.$refs.multipleTable.store.states.selection;
        if (selector.length == 0) {
           self.message("!","warning",false);
           return false;
        }
        this.deleteDlgVisible = true;
      },
      deleteDlgClose(){
        this.deleteDlgVisible=false;
      },
      //confrim delete devices
       deleteDevicesConfrim() {
        var self = this;
        var selector = this.$refs.multipleTable.store.states.selection;
        var count = 0;  //The count of delete devices successful.
        if (selector.length !== 0) {
          $.each(selector, function (i) {
            if (self.deleteDevice(this.agentId)) {
              count++;
            }
          })
        }
        if (count == selector.length) {
          var msg = "" + count + "";
          self.message(msg,"success",false);
          self.deleteDlgVisible = false;
           self.getDataByGroup(self.deviceIndex);
           //self.isGroupActive(self.groupIndex);
           self.init();
           self.getDeviceCount();
           self.getChildGroup(self.groupIndex);
           self.isDeviceActive(self.deviceIndex);
        }
      },
      handleDetail(index, data) {
        var header = {"path": "", "name": ""};
        PubSub.publish("updateHeader", header);
        this.$router.push({name: 'AddDevice', params: {label: false, itemData: data}});
      },

       handleDelete(index, data) {
        var self = this;
        var agentId = data.agentId;
        self.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          //delete confirm
          if (self.deleteDevice(agentId)) {
            self.message("","success",false);
            self.deleteDlgVisible = false;
             self.getDataByGroup(self.deviceIndex);
             //self.isGroupActive(self.groupIndex);
             self.init();
             self.getDeviceCount();
             self.getChildGroup(self.groupIndex);
             self.getChildGroup(self.groupIndex);
          }
        }).catch(() => {
          //delete cancel
          self.deleteDlgVisible = false;
        });
      },
      //device List data
      searchDevice() {
        var self = this;
        //var url = self.baseURL + "/device/find";
        let data = {
          "page": self.currentPage == 0 ? 0 : self.currentPage - 1,
          "size": self.currentSize,
          "like": self.input
        };
        if (self.groupIndex == -1) {
          data.groupId = "";
        } else {
          if (self.deviceIndex == -1) {
            data.groupId = self.groupList[self.groupIndex].id;
          }
          else {
            data.groupId = self.deviceList[self.deviceIndex].id;
          }
        }
        var tempData = [];
        //Vue.axios.put(url, data).then(res => {
        Device.getDeviceList(data).then(res=>{
          var list = res.data.content;
          if (list.length == 0) {
            self.showPagin = false;
          } else {
            self.showPagin = true;
          }
          self.pageCount = res.data.totalPages * self.currentSize;
          $.each(list, function (i) {
            var obj = {};
            obj.agentId = list[i].agentId;
            obj.status = list[i].status;
            obj.name = list[i].name;
            obj.type = list[i].osVersion;
            obj.model = list[i].model;
            obj.description = list[i].description;
            obj.cpuName = list[i].cpuName;
            obj.hddName = list[i].hddName;
            obj.boardName = list[i].boardName;
            tempData[i] = obj;
          })
          self.tableData = tempData;
          //this.deviceCount=res.data.totalElements;
        })
      },
      notify(title, msg, type) {
        var self = this;
        self.$notify({
          title: title,
          message: msg,
          type: type,
          position: 'bottom-right',
          showClose: false,
          duration: 2000
        });
      },
      message(title,type,showClose){
        var self=this;
        self.$message({
          showClose: showClose,
          message: title,
          type:type
        });
      },
      //regex the group name
      regexName(str){
        var retMsg="";
        var pattern = new RegExp("[`~!@#$^&*()=|{}':;',\\[\\].<>/?~@#&*|{}']");
        if(str==undefined||str.length==0){
          retMsg="";
        }
        if(str.length>=10){
          retMsg="10"
        }
        if(pattern.test(str)){
          retMsg="";
        }
        return retMsg;
      },
      //regex the group description
      regexDescription(str){
        var retMsg="";
        var pattern = new RegExp("[`~!@#$^&*()=|{}':;',\\[\\].<>/?~@#&*|{}']");
        if(str.length>=50){
          retMsg="50"
        }
        if(pattern.test(str)){
          retMsg="";
        }
        return retMsg;
      }
    },
    created(){
      //this.getConfigJson();
    },
    mounted(){
      var header = {"path":"/device","name": ""};
      PubSub.publish("updateHeader",header);
      var self=this;
      let url=localStorage.getItem('apiUrl');
      console.log(url);
      self.baseURL=url+"/storesense/api/v1.0";  //localStorageURL.
      if(self.tableData.length==0){
        self.showPagin=false;
      }
      self.init();            //initialize the group data on home page.
      self.getDeviceCount();  //get device count
      self.initGroupData();  //initialize the group data on move device page
      self.searchDevice();   //get the initializtion devices
    }
  }

</script>

