<template>
    <div class="el-cloud-mgr">
        <div style="width:100%;height:100px;">
            <span class="el-cloud-num"></span>
            <img :src="imgUrl" class="icon-line-status"><br>
            <hr class="el-hr-basic" color="#EBEBEB"><br>
        </div>
        <div style="width:100%;">
            <div style="overflow: hidden;width:100%;">
                <span class="el-iotkey"></span>
                <el-input class="el-input-num"  v-model="registKey" v-if="bUpdate" size="small"></el-input>
                <el-input class="el-input-num"  v-model="registKey" v-else disabled=""></el-input>
            </div>
            <div style="overflow: hidden;">
                <span class="el-cloud-url">URL</span>
                <el-input class="el-input-url"  v-model="creURL" size="small"></el-input>
            </div>
            <div style="overflow: hidden;">
                <span class="el-cloud-iotkey">IOT</span>
                <el-input class="el-input-iotkey" v-model="itoKey" size="small"></el-input>
                
            </div>
            <el-button class="el-deploy-btn" type="primary" size="medium" @click="submit"></el-button>
        </div>

    </div>
</template>
<script>
import Cloud from '../api/cloud'
export default {
    name: "CloudMgr",
    data(){
        return{
            registKey:"",
            bUpdate:true,
            creURL:"",
            itoKey:"",
            timer:null,
            imgUrl:require("../assets/not online.png")
        }
    },
    
    mounted(){
        let self=this;
        self.getData();
        self.timer= window.setInterval(function(){self.getStatus()},3000);  
    },
    beforeDestroy(){
        if(this.timer){
            clearInterval(this.timer);
        }
    },
    methods:{
        getData(){
            let self=this;
            Cloud.getConfig().then(res=>{
                console.log(res);
                self.registKey=res.data.rk;
                self.creURL=res.data.dccsUrl;
                self.itoKey=res.data.dccsKey;
            })
        },
        submit(){
            let self=this;
            let data={'dccsUrl':self.creURL,'dccsKey':self.itoKey,'rk':self.registKey};
            Cloud.setConfig(data).then(res=>{
                if(res.data.result=="true"){
                    self.message('','success',false);
                }else{
                    self.message('','warning',false);
                    return false;
                }
            })
        },
        getStatus(){
            let self=this;
            Cloud.getStatus().then(res=>{
                console.log(res.data.connStatus);
                if(res.data.connStatus==1){
                    self.imgUrl=require("../assets/online.png");
                }else{
                    self.imgUrl=require("../assets/not online.png");
                }
            })
        },
        message(title,type,showClose){
            var self=this;
            self.$message({
            showClose: showClose,
            message: title,
            type:type
            });
        }
    }
}
</script>
<style scoped>
   .el-cloud-mgr{
        width:100%;
        height: 100%;
        background-color: #FFFFFF;
        border: 0.5px solid #ebebeb;
   }
   .el-cloud-num{
       float: left;
       margin-left: 45px;
       margin-top: 40px;
       font-size: 16px;
   }
   .el-input-num{
       float: left;
       width: 500px;
       margin-left: 30px;
       margin-top: 40px;
       width: 400px;
       font-size: 14px;
   }
   .el-iotkey{
       float: left;
       margin-left: 45px;
       margin-top: 47px;
       font-size: 14px;
   }
   .el-hr-basic{
       float: left;
       margin-left: 45px;
       margin-top:30px;
       width: 90%;
       color: #EBEBEB;
       border: 1px dashed;
   }
   .el-cloud-url{
       float: left;
       margin-left: 45px;
       margin-top: 28px;
       font-size: 14px;
   }
   .el-input-url{
       float: left;
       margin-left: 58px;
       margin-top: 20px;
       width: 400px;
       font-size: 14px;
   }
   .el-cloud-iotkey{
       float: left;
       margin-left: 45px;
       margin-top: 31px;
       font-size: 13px;
   }
   .el-input-iotkey{
       float: left;
       margin-left: 68px;
       margin-top:20px;
       width: 400px;
       font-size: 14px;
   }
   .el-deploy-btn{
       float: left;
       font-size: 14px;
       margin-left: 450px;
       margin-top: 120px;
       width: 120px;
   }
   .icon-line-status{
       float: left;
       margin-left: 30px;
       margin-top: 28px;
       font-size: 30px;
   }
</style>


