<template>
  <div>
  <div class="el-device-add">
    <div style="overflow: hidden">
      <p class="el-label-add" v-if="bUpdate" ></p>
      <p class="el-label-add" v-else ></p>
      <el-button class="el-btn-update" type="primary" medium @click="updateDevice"></el-button>
    </div>
    <div style="overflow: hidden">
      <div class="el-style-basic"></div>
      <p class="el-label-basic"></p>
      <hr class="el-hr-basic" color="#EBEBEB">
    </div>
    <div style="overflow: hidden">
        <span class="el-name-star">*</span>
        <span class="el-device-name"> :</span>
        <el-input class="el-input-name" v-model="deviceName" ></el-input>

        <span class="el-id-star">*</span>
        <span class="el-agent-id">Agent ID :</span>
        <el-input class="el-input-id" v-model="agentID" v-if="bUpdate"></el-input>
        <el-input class="el-input-id" v-model="agentID" v-else disabled=""></el-input>
    </div>
    <div style="overflow: hidden">
      <span class="el-device-type"> :</span>
      <el-input class="el-input-type" v-model="deviceType"></el-input>
    </div>
    <div style="overflow: hidden">
      <span class="el-label-description"></span>
    </div>
    <div style="overflow: hidden">
      <textarea class="el-description" v-model="description"></textarea>
    </div>
    <div style="overflow: hidden">
      <el-checkbox class="el-checkbox" v-model="checked" @change="handleCheckedChange"></el-checkbox>

      <el-select v-model="value" placeholder="" id="sel_group" v-on:change="indexSelect" v-bind:disabled="isAble">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
    </div>
    </div>
    <div id="config-info">
    <div v-show="!bUpdate">
      <div style="overflow: hidden" >
        <div class="el-style-config"></div>
        <p class="el-label-config"></p>
        <hr class="el-hr-config" color="#EBEBEB">
      </div>

      <div style="overflow: hidden">
        <span class="el-label-os"></span>
        <span class="el-text-os" v-model="configuration.os">{{configuration.os}}</span>
      </div>
      <div style="overflow: hidden">
        <span class="el-label-processor"></span>
        <span class="el-text-processor" v-model="configuration.processor">{{configuration.processor}}</span>
      </div>
      <div style="overflow: hidden">
        <span class="el-label-board"></span>
        <span class="el-text-board" v-model="configuration.board">{{configuration.board}}</span>
      </div>
      <div style="overflow: hidden">
        <span class="el-label-memory"></span>
        <span class="el-text-memory" v-model="configuration.memory">{{configuration.memory}}</span>
      </div>
      <div style="overflow: hidden">
        <span class="el-label-hdd"></span>
        <span class="el-text-hdd" v-model="configuration.hdd">{{configuration.hdd}}</span>
      </div>
    </div>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue';
  import Device from '../api/device'
  import Group from '../api/group'
    export default {
        name: "AddDevice",
        data(){
          return{
            deviceName:'',
            agentID: '',
            deviceType: '',
            description: '',
            options: [],
            value: '',
            bUpdate: true,
            configuration:{
              os: '',
              processor:'',
              board:'',
              memory:'',
              hdd:''
            },
            groupId:"",
            checked:false,
            baseURL:"",
            isAble:false
          }
        },
      mounted(){
        let url=localStorage.getItem('apiUrl');
        console.log(url);
        this.baseURL=url+"/storesense/api/v1.0";  //localStorageURL.
        this.initData();
      },
      created(){

        this.bUpdate = this.$route.params.label;
        let itemData = this.$route.params.itemData;
        this.groupId=this.$route.params.groupId;
        if(!this.checked){
          this.isAble=true;
        }
        if(itemData !== null) {
          this.deviceName = itemData.name;
          this.agentID=itemData.agentId;
          this.deviceType=itemData.model;
          this.description=itemData.description;
          this.configuration.os=itemData.type;
          this.configuration.processor=(itemData.cpuName==null||itemData.cpuName.length==0)?"---":itemData.cpuName;
          this.configuration.board=(itemData.boardName==null||itemData.boardName.length==0)?"---":itemData.boardName;
          this.configuration.memory="---";
          this.configuration.hdd=(itemData.haddName==null||itemData.hddName.length==0)?"---":itemData.hddName;

        }

      },
      methods:{
        initData(){ 
          var self=this;
          //var urlGetGroup = self.baseURL + "/group/list";
          var urlgroupId=self.baseURL + "/group/get_group?agentId="+self.agentID;
          Group.getGroupList().then((res) => {
            var result = res.data.data;
            $.each(result, function (i) {
              if (this.children.length == 0) { //this group is not have children groups.
                return true;
              } else {
                console.log(i);
                var parentName = this.name;  //the  parent  group name
                var child = this.children;
                $.each(child, function (j) {
                  self.options.push({"value": this.id, "label": parentName + "/" + this.name});
                })
              }
            })
            if(self.agentID.length==0){  //add device
              // self.checked=true;
              // if(self.groupId.length!=0){
              //    self.value = self.groupId;
              //  }else{
              //    self.value = self.options[0].value;
              // }
              return;
            }else{  //modify device
              //Group.getGroupByDevice(self.agentID).then((res) => {
              Group.getGroupByDevice(self.agentID).then((res) => {
                var id=res.data.id;
                if(id!=undefined&&id.length!=0){
                  self.checked=true;
                  self.isAble=false;
                  //self.value=self.options[id].value;
                  self.value=id;
                }
              })
            }

          }).catch(err => {
            console.log(err.message);
          })
        },
        //modify device by it's id.
        modifyDevice(){
          var self=this;
          var deviceName=this.deviceName;
          var agentID=this.agentID;
          var deviceType=this.deviceType;
          var description=this.description;
          if(deviceName.length==0){
            self.message("!","warning",false);
            return false;
          }
          //var urlEdit = self.baseURL + "/device/edit";
          let data = {"agentId": agentID, "name": deviceName, "description": description, "model": deviceType};
          let groupdata = {"groupId": this.value, "agentId": [agentID]};
          if (!self.checked) {  //modity the device
           Device.editDevice(data).then((res) => {
              if (res.data.result) {
                self.message("!", "success", false);
                self.$router.push({path: "/"});
              } else {
                self.message("!", "warning", false);
                return false;
              }
            }).catch(err => {
              console.log(err.message);
              self.message("", "error", false);
              return false;
            })
          }
          else {  //modity and grouping the device.
            if(self.value.length==0){
              self.message("!", "warning", false);
              return false;
            }
            //var url = self.baseURL + "/group/put_device";
            Device.editDevice(data).then((res) => {
              if (res.data.result) {
                Group.putDevice(groupdata).then((res) => {
                  if (res.data.result) {
                    self.message("!", "success", false);
                    self.$router.push({path: "/"});
                  } else {
                    self.message("!", "warning", false);
                    return false;
                  }
                })
              } else {
                self.message("!", "warning", false);
                return false;
              }
            }).catch(err => {
              console.log(err.message);
              self.message("!", "error", false);
            })
          }
        },
        //add a new device
        addDevice(){  //
           var self=this;
           var deviceName=this.deviceName;
           var agentID=this.agentID;
           var deviceType=this.deviceType;
           var description=this.description;
           if(deviceName.length==0||agentID.length==0){
             self.message("ID!","warning",false);
             return false;
           }
           let data={"agentId":agentID,"name":deviceName,"description":description,"model":deviceType};
           let groupdata={"groupId":this.value,"agentId":[agentID]};
            //var urlAdd = self.baseURL + "/device/add";
            if (!self.checked) {  //
              Device.addDevice(data).then((res) => {
                if (res.data.result) {
                  self.message("!", "success", false);
                  self.$router.push({path: "/"});
                } else {
                  self.message("!", "warning", false);
                  return false;
                }
              }).catch(err => {
                console.log(err.message);
                self.message("!", "error", false);
              })
            } 
            else {  //
              if(self.value.length==0){
                self.message("!", "warning", false);
                return false;
              }
              var url = self.baseURL + "/group/put_device";
              Device.addDevice(data).then((res) => {
                if (res.data.result) {
                  Group.putDevice(groupdata).then((res) => {
                    if (res.data.result) {
                      self.message("!", "success", false);
                      self.$router.push({path: "/"});
                    } else {
                      self.message("!", "warning", false);
                      return false;
                    }
                  }).catch(err => {
                    console.log(err.message);
                    self.message("!", "error", false);
                    return false;
                  })
                } else {
                  self.message("!", "warning", false);
                  return false;
                }
              }).catch(err => {
                console.log(err.message);
                self.message("!", "error", false);
              })
            }
        },
        //
        updateDevice(){
          //modify device
          if(this.bUpdate!=undefined&&!this.bUpdate){
            this.modifyDevice();
          }
          else{
            this.addDevice();
          }
        },
        handleCheckedChange(val){
          if(val){ //checked
            this.isAble=false;
          }else{
            this.isAble=true;
          }
        },
        notify(title, msg, type) {
          var self = this;
          self.$notify({
            title: title,
            message: msg,
            type: type,
            position: 'bottom-right',
            showClose: false,
            duration: 1000
          });
        },
        message(title,type,showClose){
          var self=this;
          self.$message({
            showClose: showClose,
            message: title,
            type:type
          });
        },
      }
    }
</script>

<style scoped>
