  }

  .el-input-name{
    float: left;
    width: 350px;
    margin-top: 17px;
    margin-left: 25px;
  }

  .el-id-star{
    color: red;
    font-size: 20px;
    float: left;
    margin-left: 100px;
    margin-top: 28px;
  }

  .el-agent-id{
    float: left;
    margin-left: 2px;
    margin-top: 28px;
  }

  .el-input-id{
    float: left;
