<template>
  <div id="app">
    <!--<img src="./assets/logo.png">-->
    <el-container>
      <el-header>
        <header-bar></header-bar>
      </el-header>
      <el-container class="el-container-body" id="el-body">
        <el-aside width="15%">
          <aside-bar></aside-bar>
        </el-aside>
        <el-main>
          <router-view v-if="isRouterAlive"></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
  import AsideBar from './components/AsideBar';
  import HeaderBar from './components/HeaderBar';

  export default {
    name: 'App',
    provide(){
      return{
        reload:this.reload
      }
    },
    data(){
      return{
        isRouterAlive:true
      }
    },
    components:{
      AsideBar: AsideBar,
      HeaderBar: HeaderBar
    },
    methods:{
      handleOpen(data){

      },
      reload(){
        this.isRouterAlive=false;
        this.$nextTick(function(){
          this.isRouterAlive=true;
        })
      }
    },
    mounted(){
      function findDimensions() {
        document.getElementById("el-body").style.height= document.documentElement.clientHeight - 60 +"px";
      }

      findDimensions();
      window.onresize=findDimensions;
    }
}
</script>

<style scoped
lang="scss">
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
$webket-auto: -webkit-fill-available;
.el-header{
  background-color: #FFFFFF;
  color: #333;
  text-align: left;
  line-height: 60px;
}

.el-container-body{
  display: flex;
  width: 100%;
  /*height: 70%;*/
  /*height: 600px;*/
}

.el-aside {
  background-color: #27304D;
  color: #333;
  text-align: center;
  line-height: 400px;
  overflow-x: hidden;
  overflow-y: hidden;
}

.el-main {
  background-color: #F7FAFF;
  color: #333;
  flex: 1;
  flex-basis: auto;
  padding: 20px;
  overflow-x: hidden;
  overflow-y: auto;
}

</style>



