<template>
  <div class="el-group-mgr" style="overflow: hidden;">
    <div class="el-group-edit">
      <div style="overflow: hidden">
        <p class="el-label-group"></p>
        <p class="el-group-add" @click="createGroup()"></p>
        <i class="el-icon-circle-plus" id="icon-group-add" @click="createGroup()"></i>
      </div>
      <div style="overflow: hidden">
        <hr class="el-hr-group" color="#EBEBEB">
      </div>
      <div style="overflow: hidden" v-show="is_groupTitle">
         <span class="el-group-tip">...</span>
      </div>
      <div style="margin-top: 10px;overflow-y: auto;overflow-x:hidden;max-height: 550px">
        <div v-for="(item,index) in groupList" :key="index" >
          <div class="el-group-btn" type="text" @click="selectGroup(index)"
                     :class="[groupIndex == index ? 'el-group-active' : 'el-group-default']">
            <span class="el-group-name">{{item.name}}</span>
            <i class="el-icon-edit-outline" id="group-icon-edit"
               v-show="groupIndex == index ? true : false" @click.stop="editGroup(index)"></i>
            <i class="el-icon-close" id="group-icon-delete"
               v-show="groupIndex == index ? true : false" @click.stop="deleteGroup(index)"></i>
          </div>
          <div class="el-group-item" v-show="item.editShow">
            <el-input class="el-input-name" v-model="itemName"></el-input>
            <i class="el-icon-check" id="group-item-ok" @click="modifyGroup(index)"></i>
            <i class="el-icon-close" id="group-item-close" @click="closeGroup(index)"></i>
            <el-input class="el-input-description" v-model="itemDescription"
                      type="textarea" rows=3></el-input>

          </div>
        </div>
      </div>
      <div style="overflow: hidden" v-show="addGroup" >
        <el-input class="input-group-name" v-model="groupName" placeholder=""></el-input>
        <i class="el-icon-check" id="icon-group-ok" @click="addGroups()"></i>
        <i class="el-icon-close" id="icon-group-close" @click="closeGroups()"></i>
        <el-input class="input-group-description" v-model="groupDescription"
                  type="textarea" rows=5 placeholder=""></el-input>

      </div>
    </div>

    <div class="el-device-edit">
      <div style="overflow: hidden">
        <p class="el-label-device">{{deviceListTitle}}</p>
        <p class="el-device-add" @click="createDevice()" v-show="groupList.length == 0 ? false :true"></p>
        <i class="el-icon-circle-plus" id="icon-device-add" @click="createDevice()" v-show="groupList.length == 0 ? false :true"></i>
      </div>
      <div style="overflow: hidden">
        <hr class="el-hr-device" color="#FFFFFF">
      </div>
      <div style="overflow: hidden;margin-top: 10px;overflow-y: auto;overflow-x:hidden;max-height: 550px">
        <div v-for="(item,index) in deviceList" :key="index">
          <div class="el-device-btn" type="text" @click="selectDevice(index)"
                     :class="[deviceIndex == index ? 'el-device-active' : 'el-device-default']">
            <span class="el-group-name">{{item.name}}</span>
            <i class="el-icon-edit-outline" id="dev-icon-edit"
               v-show="deviceIndex == index ? true : false" @click.stop="editDevice(index)"></i>
            <i class="el-icon-close" id="dev-icon-delete"
               v-show="deviceIndex == index ? true : false" @click.stop="deleteDevice(index)"></i>
          </div>
          <div class="el-device-item" v-show="item.editShow">
            <el-input class="el-input-name" v-model="devItemName"></el-input>
            <i class="el-icon-check" id="device-item-ok" @click="modifyDevice(index)"></i>
            <i class="el-icon-close" id="device-item-close" @click="closeDevice(index)"></i>
            <el-input class="el-input-description" v-model="devItemDescription"
                      type="textarea" rows=3></el-input>

          </div>
        </div>
      </div>
      <div style="overflow: hidden" v-show="addDevice" >
        <el-input class="input-device-name" v-model="devName" placeholder=""></el-input>
        <i class="el-icon-check" id="icon-device-ok" @click="addDevices()"></i>
        <i class="el-icon-close" id="icon-device-close" @click="closeDevices()"></i>
        <el-input class="input-device-description" v-model="devDescription"
                  type="textarea" rows=5 placeholder=""></el-input>

      </div>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue';
  //import Conf from '../config';
  import Group from '../api/group';
    export default {
      name: "GroupMgr",
      data(){
        return{
          addGroup: false,
          editGroupName: '',
          editGroupDes: '',
          groupName: '',
          groupDescription: '',
          itemName: '',
          itemDescription: '',
          groupIndex: 0,
          groupList:[],
          addDevice: false,
          devName:'',
          devDescription:'',
          devItemName:'',
          devItemDescription:'',
          deviceIndex: 0,
          deviceList:[],
          checkList: ['',' A'],
          deviceListTitle:"...",
          baseURL:"",
          deleteDlgVisible:false,
          is_groupTitle:false
        }
      },
      created(){},
      mounted(){
         //let url=localStorage.getItem('apiUrl');
         //console.log(url);
        // this.baseURL=url+"/storesense/api/v1.0";  //localStorageURL.
         this.initData();
      },
      methods:{
        initData(){
          var self=this;
          //var url = self.baseURL + "/group/list";
          var tempData = [];
          Group.getGroupList().then((res) => {
            var groupList = res.data.data; //
            //
            $.each(groupList, function (i) {
              var obj = {};
              obj.id = groupList[i].id;
              obj.name = groupList[i].name;
              obj.description = groupList[i].description;
              obj.count = groupList[i].count;
              obj.editShow = false;
              tempData[i] = obj;
            })
            self.groupList = tempData;
            if (groupList.length > 0) {
              self.is_groupTitle=false;
              self.deviceListTitle = self.groupList[0].name;
            }
            else {
              self.is_groupTitle=true;
              self.deviceListTitle = "...";
            }
          }).then((res) => {
            this.selectGroup(0);
          })
        },
        createGroup(){
          var self=this;
          self.is_groupTitle=false;
          if(self.groupList.length>=5){
            self.message("5!","warning",false);
            return false;
          }
          self.groupList.forEach(function (item) {
            item.editShow = false;
          })
          self.addGroup = true;
          self.deviceList.forEach(function (item) {
            item.editShow = false;
          })
          self.addDevice = false;
          self.groupName="";
          self.groupDescription="";
        },
        modifyGroup(index){
          var self=this;
          self.groupList[index].name = this.itemName;
          self.groupList[index].description = this.itemDescription;

          // self.baseURL = result.data.ApiUrl + ":" + result.data.ApiPort + "/storesense/api/v1.0";
          // var url = self.baseURL + "/group/edit";
          let data = {
            "name": this.itemName,
            "description": this.itemDescription,
            "groupId": this.groupList[index].id
          };
          Group.editGroup(data).then((res) => {
            if (res.data.result) {
              self.message("!", "success", false);
              self.groupList[index].editShow = false;
            }
          })
        },
        closeGroup(index){
          this.groupList[index].editShow = false;
        },
        addGroups(){
          var self=this;
          var flag=false;
          var item = {
            name: this.groupName,
            editShow:false,
            description:this.groupDescription
          };
          $.each(self.groupList,function(i){
            if(this.name===item.name){
              flag=true;
            }
          })
          if(flag){
            self.message("!","warning",false);
            return false;
          }
          if(self.groupList.length>=5){
            self.message("5!","warning",false);
            return false;
          }

          var nameMsg=self.regexName(self.groupName);
          var desMsg=self.regexDescription(self.groupDescription);
          if(nameMsg.length!=0){
            self.message(nameMsg,"warning",false);
            return false;
          }
          if(desMsg.length!=0){
            self.message(desMsg,"warning",false);
            return false;
          }

          let data={"name":this.groupName,"description":this.groupDescription,"parentId": -1};
          //var url = self.baseURL + "/group/add";
          Group.addGroup(data).then((res) => {
            if (res.data.result) {
              self.groupList.push(item);
              self.message("!", "success", false);
              self.groupName = "";
              self.groupDescription = "";
              self.addGroup = false;
              self.afterHandelGroup();
            }
          }).catch(error => {
            self.message("!", "error", false);
          })
        },
        afterHandelGroup(){
          var self=this;
          //var url = self.baseURL + "/group/list";
          var tempData = [];
          Group.getGroupList().then((res) => {
            var groupList = res.data.data; //
            //
            $.each(groupList, function (i) {
              var obj = {};
              obj.id = groupList[i].id;
              obj.name = groupList[i].name;
              obj.description = groupList[i].description;
              obj.count = groupList[i].count;
              obj.editShow = false;
              tempData[i] = obj;
            })
            self.groupList = tempData;
            if (self.groupList.length == 0) {
              self.deviceListTitle = "...";
              self.is_groupTitle=true;
              self.deviceList.length = 0;
            } else {
              self.deviceListTitle = self.groupList[0].name;
              self.selectGroup(self.groupList.length - 1);
            }
          })
        },
        closeGroups(){
          this.addGroup = false;
        },
        selectGroup(index){
          let self=this;
          self.groupIndex = index;
          self.groupList.forEach(function (item) {
            item.editShow = false;
          })
          self.addDevice=false;
          self.deviceListTitle=this.groupList[index].name;
          var parentId=self.groupList[index].id;
          //var url = self.baseURL + "/group/detail?groupId=" + parentId;
          var tempData = [];
          Group.getGroupDetail(parentId).then(res => {
            var list = res.data.children;
            $.each(list, function (i) {
              var obj = {};
              obj.id = list[i].id;
              obj.name = list[i].name;
              obj.description = list[i].description;
              obj.count = list[i].count;
              obj.editShow = false;
              tempData[i] = obj;
            })
            this.deviceList = tempData;
          })
        },
        editGroup(index){
          this.addGroup = false;
          this.itemName = this.groupList[index].name;
          this.itemDescription = this.groupList[index].description;
          this.groupList[index].editShow = true;
          this.deviceList.forEach(function (item) {
            item.editShow = false;
          })
          this.addDevice = false;
        },
        deleteGroup(index){
          var self=this;
          self.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            var groupId = this.groupList[index].id;
            //var url = self.baseURL + "/group/delete?groupId=" + groupId;
            Group.deleteGroup(groupId).then(res => {
              if (res.data.result) {
                self.message("!", "success", false);
                self.groupList.splice(index, 1);
                self.afterHandelGroup();
              }
            });
          }).catch(() => {
            //delete cancel
            self.deleteDlgVisible = false;
          });
        },
        createDevice(){
          var self=this;
          if(self.deviceList.length>=5){
            self.message("5!","warning",false);
            return false;
          }
          self.deviceList.forEach(function (item) {
            item.editShow = false;
          })
          self.addDevice = true;

          self.groupList.forEach(function (item) {
            item.editShow = false;
          })
          self.addGroup = false;
          self.devName="";
          self.devDescription="";
        },
        modifyDevice(index){
          var self=this;
          this.deviceList[index].name = this.devItemName;
          this.deviceList[index].description = this.devItemDescription;
          var url = self.baseURL + "/group/edit";
          let data = {
            "name": this.devItemName,
            "description": this.devItemDescription,
            "groupId": this.deviceList[index].id
          };
          Group.editGroup(data).then((res) => {
            if (res.data.result) {
              self.message("!", "success", false);
              self.deviceList[index].editShow = false;
            }
          })
        },
        closeDevice(index){
          this.deviceList[index].editShow = false;
        },
        selectDevice(index){
          this.deviceIndex = index;
          this.deviceList.forEach(function (item) {
            item.editShow = false;
          })
        },
        editDevice(index){
          this.addDevice = false;
          this.devItemName = this.deviceList[index].name;
          this.devItemDescription = this.deviceList[index].description;
          this.deviceList[index].editShow = true;
          this.groupList.forEach(function (item) {
            item.editShow = false;
          })
          this.addGroup = false;
        },
        deleteDevice(index){
          var self=this;
          var groupId=self.deviceList[index].id;
          self.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            //var url = self.baseURL + "/group/delete?groupId=" + groupId;
            Group.deleteGroup(groupId).then(res => {
              if (res.data.result) {
                self.message("!", "success", false);
                self.deviceList.splice(index, 1);
              }
            });
          }).catch(() => {
            //delete cancel
            self.deleteDlgVisible = false;
          });
        },
        addDevices(){
          var self=this;
          var flag=false;
          var item = {
            name: this.devName,
            editShow:false,
            description:this.devDescription
          };

          $.each(self.deviceList,function(i){
            if(this.name===item.name){
              flag=true;
            }
          })
          if(flag){
            self.message("!","warning",false);
            return false;
          }
          if(self.deviceList.length>=5){
            self.message("5!","warning",false);
            return false;
          }

          var nameMsg=self.regexName(self.devName);
          var desMsg=self.regexDescription(self.devDescription);
          if(nameMsg.length!=0){
            self.message(nameMsg,"warning",false);
            return false;
          }
          if(desMsg.length!=0){
            self.message(desMsg,"warning",false);
            return false;
          }
            //var url = self.baseURL + "/group/add";
            var parentGroupId = this.groupList[this.groupIndex].id;
            let data = {"name": this.devName, "description": this.devDescription, "parentId": parentGroupId};
            Group.addGroup(data).then((res) => {
              if (res.data.result) {
                self.message("!", "success", false);
                self.deviceList.push(item);
                self.addDevice = false;
                self.selectGroup(self.groupIndex);
              }
            })
        },
        closeDevices(){
          this.addDevice = false;
        },
        notify(title, msg, type) {
          var self = this;
          self.$notify({
            title: title,
            message: msg,
            type: type,
            position: 'bottom-right',
            showClose: false,
            duration: 2000
          });
        },
        message(title,type,showClose){
          var self=this;
          self.$message({
            showClose: showClose,
            message: title,
            type:type
          });
        },
        regexName(str){
          var retMsg="";
          var pattern = new RegExp("[`~!@#$^&*()=|{}':;',\\[\\].<>/?~@#&*|{}']");
          if(str==undefined||str.length==0){
            retMsg="";
          }
          if(str.length>=10){
            retMsg="10"
          }
          if(pattern.test(str)){
            retMsg="";
          }
          return retMsg;
        },
        regexDescription(str){
          var retMsg="";
          var pattern = new RegExp("[`~!@#$^&*()=|{}':;',\\[\\].<>/?~@#&*|{}']");
          if(str.length>=50){
            retMsg="50"
          }
          if(pattern.test(str)){
            retMsg="";
          }
          return retMsg;
        }
      }
    }
</script>

