<template>
    <div class="el-aside-bar">
      <el-menu
        default-active="1-1"
        class="el-menu-vertical-demo"
        @open="handleOpen"
        @close="handleClose"
        background-color="#27304D"
        text-color="#fff"
        active-text-color="#ffd04b"
        default-openeds="[1,2,3,4]"
        router>
        <!--<span></span>-->
        <el-submenu index="1">
          <template slot="title">
            <span></span>
          </template>
          <el-menu-item index="1-1" @click="deviceCtrl" route="/device">
            <i class="iconfont icon-shebeiguanli"></i>
            <span>{{deviceMgr}}</span>
          </el-menu-item>
          <el-menu-item index="1-2"  @click="groupCtrl" route="/group">
            <i class="iconfont icon-shebeifenzuguanli"></i>
            <span>{{groupMgr}}</span>
          </el-menu-item>
        </el-submenu>
        <el-submenu index="2" disabled>
          <template slot="title">
            <span></span>
          </template>
          <el-menu-item index="2-1" disabled>
            <i class="iconfont icon-yichangtiaojianshezhi1"></i>
            <span></span>
          </el-menu-item>
          <el-menu-item index="2-2" disabled>
            <i class="iconfont icon-yichangshijianliebiaocopy"></i>
            <span></span>
          </el-menu-item>
        </el-submenu>
        <el-submenu index="3">
          <template slot="title">
            <span></span>
          </template>
          <el-menu-item index="3-1" @click="cloudCtrl" route="/cloud">
            <i class="iconfont icon-shezhi"></i>
            <span>{{cloudMgr}}</span>
          </el-menu-item>
        </el-submenu>
      </el-menu>
    </div>
</template>

<script>
    import PubSub from 'pubsub-js';
    export default {
      name: "AsideBar",
      data() {
        return {
          isCollapse: false,
          deviceMgr: '',
          groupMgr: '',
          cloudMgr:''
        };
      },
      methods: {
        handleOpen(key, keyPath) {
          console.log(key, keyPath);
        },
        handleClose(key, keyPath) {
          console.log(key, keyPath);
        },
        deviceCtrl(){
          var header = {"path":"/device","name": this.deviceMgr};
          PubSub.publish("updateHeader",header);
        },
        groupCtrl(){
          var header = {"path":"/group","name": this.groupMgr};
          PubSub.publish("updateHeader",header);
        },
        cloudCtrl(){
          var header={"path":"/cloud","name":this.cloudMgr};
          PubSub.publish("updateHeader",header);
        }
      },
      mounted(){
        this.deviceCtrl();
      }
    }
</script>

<style scoped>
  .el-aside-bar{
    width: 100%;
    border-style: solid;
    border-width: 1px;
    border-color: #27304D;
  }

  .el-menu {
    list-style: none;
    vertical-align: top;
    text-align: left;
  }
  .iconfont{
    font-size: 24px;
    margin-right: 15px;
  }
</style>



