import {fetch} from './ajax'

const baseURL=localStorage.getItem('apiUrl');
const PREFIX=`/storesense/api/v1.0`;

function timeURL(url) {
    var time = (new Date()).valueOf();

     if(url.indexOf('?')>=0){
          url = url +'&t='+time;
    }else{
         url = url + '?t=' + time;
   }
    return url;
}

class Group{
    static getGroupList(){
        let url=`/group/list`;
        let config={
            url:timeURL(`${baseURL}${PREFIX}${url}`),
            method:'GET'
        };
        return fetch(config);
    }
    static getGroupDetail(groupId){
        let url=`/group/detail?groupId=${groupId}`;
        let config={
            url:timeURL(`${baseURL}${PREFIX}${url}`),
            method:'GET'
        };
        return fetch(config);
    }
    static addGroup(data){
        let url="/group/add";
        let config={
            url:timeURL(`${baseURL}${PREFIX}${url}`),
            method:'POST',
            data:data
        };
        return fetch(config);
    }
    static editGroup(data){
        let url="/group/edit";
        let config={
            url:timeURL(`${baseURL}${PREFIX}${url}`),
            method:'PUT',
            data:data
        };
        return fetch(config);
    }
    static deleteGroup(groupId){
        let url=`/group/delete?groupId=${groupId}`;
        let config={
            url:timeURL(`${baseURL}${PREFIX}${url}`),
            method:'DELETE'
        };
        return fetch(config);
    }
    static putDevice(data){
        let url=`/group/put_device`;
        let config={
            url:timeURL(`${baseURL}${PREFIX}${url}`),
            method:'POST',
            data:data
        };
        return fetch(config);
    }
    static  getGroupByDevice(agentId){
        let url=`/group/get_group?agentId=${agentId}`;
        let config={
            url:timeURL(`${baseURL}${PREFIX}${url}`),
            method:'GET'
        };
        return fetch(config);
    }
}

export default Group;


