<template>
  <div class="el-device-assign">
    <div style="overflow: hidden">
      <p class="el-label-title" v-model="deviceCount">{{deviceCount}}</p>
      <el-button class="el-btn-update" type="primary" medium @click="assignDevice"></el-button>
    </div>
    <div style="overflow: hidden">
      <el-table
        ref="multipleTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%;height: 77%"
        height=720
        @selection-change="handleSelectionChange">
        <el-table-column
          type="selection"
          header-align="center"
          width="70">
        </el-table-column>
        <el-table-column
          align="left"
          prop="name"
          label=""
          min-width="15%">
          <template slot-scope="scope">{{ scope.row.name }}</template>
        </el-table-column>
        <el-table-column
          align="left"
          prop="id"
          label="Agent ID"
          min-width="30%">
        </el-table-column>
        <el-table-column
          align="left"
          prop="ip"
          label="IP"
          min-width="15%"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          align="left"
          prop="mac"
          label="MAC"
          min-width="25%">
        </el-table-column>
        <el-table-column
          align="left"
          prop="osType"
          label="OS"
          min-width="25%">
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue';
  import Device from '../api/device';
  export default {
      name: "AssignDevice",
      data(){
        return{
          deviceCount: 0,
          tableData: [],
          checkData:[],
          baseURL:"",
          groupId:""
        }
      },
    inject:['reload'], //reload
    created(){
      self.groupId=this.$route.params.groupId;
    },
    mounted(){
        let url=localStorage.getItem('apiUrl');
        console.log(url);
        this.baseURL=url+"/storesense/api/v1.0";  //localStorageURL.
        this.getData();
    },
    methods:{
        handleSelectionChange(val){
          this.checkData = val;
        },
        assignDevice(){
          var self=this;
          var url = self.baseURL + "/device/add";
          if (self.checkData.length == 0) {
            self.message("!", "warning", false);
            return false;
          }
          else {
            var count = 0;
            $.each(self.checkData, function (i) {
              let data = {
                "agentId": this.id,
                "name": this.name,
                "description": this.description,
                "model": this.model
              };
              $.ajax({
                url: url,
                type: 'POST',
                data: JSON.stringify(data),
                async: false,
                contentType: "application/json",
                timeout: 5000,//
                dataType: 'json', //
                success: function (res) {
                  if (res.result) {
                    count++;
                  }
                }
              })
            })
            if (count == self.checkData.length) {
              var msg = "" + count + "";
              self.message(msg, "success", false);
              self.reload();
            }
            self.$router.push({path: "/"});
          }
        },
        /**
         * 
         */
        getData(){
          let self=this;
          //var url = self.baseURL + "/device/unassign";
          var tempData = [];
           Device.getUnassignDevice().then(res => {
            self.deviceCount = res.data.totalCount;
            var list = res.data.data;
            $.each(list, function (i) {
              var obj = {};
              obj.id = this.agentId;
              obj.name = this.name;
              obj.ip = this.agentIp;
              obj.mac = this.mac;
              obj.osType = this.osVersion;
              obj.description = this.description;
              obj.model = this.model;
              tempData[i] = obj;
            })
            self.tableData = tempData;
          })
        },
        notify(title, msg, type) {
          var self = this;
          self.$notify({
            title: title,
            message: msg,
            type: type,
            position: 'bottom-right',
            showClose: false,
            duration: 2000
          });
        },
        message(title,type,showClose){
          var self=this;
          self.$message({
            showClose: showClose,
            message: title,
            type:type
          });
        },
      }
  }
</script>

<style scoped>
  .el-device-assign{
    /*height: 100%;*/
    background-color: #FFFFFF;
    border: 0.5px solid #ebebeb;
  }
  .el-label-title{
    float: left;
    margin-left: 20px;
    margin-top: 30px;
    font-size: 16px;
  }

  .el-btn-update{
    float: right;
    margin-right: 20px;
    margin-top: 20px;
    display: inline-block;
    width: 120px;
  }

  .el-table{
    float: left;
    margin-top: 20px;
    margin-bottom: 10px;
    height: 60%;
  }
</style>



