import Vue from 'vue'
import Router from 'vue-router'
import DeviceMgr from '@/components/DeviceMgr'
import GroupMgr from '@/components/GroupMgr'
import AddDevice from '@/components/AddDevice'
import AssginDevice from '@/components/AssignDevice'
import CloudMgr from '@/components/CloudMgr'
Vue.use(Router)

export default new Router({
   //mode:'history',
   //base:'/storesense/iSENSE.Edge',
  routes: [
    {
      path: '/',
      component: DeviceMgr
    },
    {
      path: '/device',
      name: 'DeviceMgr',
      component: DeviceMgr
    },
    {
      path: '/group',
      name: 'GroupMgr',
      component: GroupMgr
    },
    {
      path: '/device/add',
      name:'AddDevice',
      component: AddDevice
    },
    {
      path: '/device/assign',
      name: 'AssignDevice',
      component: AssginDevice
    },
    {
      path:'/cloud',
      name:'CloudMgr',
      component:CloudMgr
    },
    {
      path: '*',
      redirect: '/'
    }
  ]
})



